@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
*,
*::after,
*::before {
    padding    : 0;
    margin     : 0;
    box-sizing : border-box;
}

body {
    font-family      : "Open Sans", sans-serif;
    background-color : #172b33;
}

.container {
    position       : relative;
    display        : flex;
    flex-direction : column;
    width          : 640px;
    margin         : 50px auto;
}

.container__top {
    display         : flex;
    justify-content : space-between;
    margin          : 0 5px;
    color           : #fff;
}

.game-end {
    position         : absolute;
    top              : 50%;
    left             : 50%;
    transform        : translate(-50%, -50%);
    z-index          : 1000;
    background-color : #117d00;
    border-radius    : 5px;
    width            : 250px;
    padding          : 10px;
    text-align       : center;
    font-size        : 24px;
    color            : #fff;
    border           : 2px solid #0d191d;
    cursor           : pointer;
    transition       : all 400ms;
}

.game-end:hover {
    background-color : #0c4a00;
}

.cards {
    display         : flex;
    flex-wrap       : wrap;
    justify-content : center;
}

.card {
    position         : relative;
    width            : 150px;
    height           : 150px;
    perspective      : 150em;
    background-color : #c52940;
    margin           : 5px;
    border-radius    : 5px;
    cursor           : pointer;
}

.card__side {
    display             : flex;
    align-items         : center;
    justify-content     : center;
    border-radius       : 5px;
    position            : absolute;
    top                 : 0;
    left                : 0;
    width               : 100%;
    height              : inherit;
    -webkit-backface-visibility : hidden;
            backface-visibility : hidden;
    transition          : all .6s ease;
    box-shadow          : 1em 1em 2em rgba(0, 0, 0, 0.2);
}

.card__side--front {
    font-size      : 94px;
    text-transform : uppercase;
    font-weight    : bold;
    color          : #cea1a8;
}

.card__side--back {
    transform        : rotateY(180deg);
    background-color : #fff;
}

.card--flipped .card__side--front {
    transform : rotateY(-180deg);
}

.card--flipped .card__side--back {
    transform : rotate(0);
}

